import React from "react";
import {Table} from 'react-bootstrap'
import { connect, useDispatch, useSelector } from "react-redux";

function Cart(props){

    let state = useSelector((state)=>state)
    
    let dispatch = useDispatch();


    return (
        <div>
<Table striped bordered hover variant="dark">
  <thead>
    <tr>
      <th>#</th>
      <th>상품명</th>
      <th>수량</th>
      <th>변경</th>
    </tr>
  </thead>
  <tbody>

      {
          state.reducer.map((v,i)=>{
              return (
                <tr key={i}>
                    <td>{v.id}</td>
                    <td>{v.name}</td>
                    <td>{v.quan}</td>
                    <td>
                        <button onClick={()=>{
                            
                            dispatch({type : '수량증가', payload:{i : v.id}})}}>
                            +
                        </button>
                        <button onClick={()=>{
                            dispatch({type : '수량감소' , payload:{i : v.id}})}}>
                            -
                        </button>    
                    </td>

                </tr>
              )
          })
      }
    
    
  </tbody>
</Table>

{
    props.alert열렸니 === true 
    ? (<div className="my-alert2" style={{backgroundColor:"yellowgreen",padding:"20px" }}>
    <p style={{marginBottom:"0", color:"white", fontSize:"24px", fontWeight:"bold"}}>지금 구매하시면 신규할인 20%</p>
    <button onClick={()=>{
        props.dispatch({type : 'alert닫기'})
    }}>닫기</button>
    </div>)
    : null
}




        </div>
    )
}

// function state를props화(state){
  
//     return {
//         state : state.reducer,
//         alert열렸니 : state.reducer2

        
//     }
// }

export default Cart;